import React from 'react';
import '../style/Intro.scss';

export default class Intro extends React.Component {

  render() {
    return(
      <div className="intro-wrapper">
        <div className="intro">
          <h2>Who</h2>
          <p>NUACM is a club devoted to the field of Computer Science. </p>

          <h2>What</h2>
          <p>We host weekly tech talks hosted by innovators in industry and academia. Our talks cover a wide variety of
          topics, from machine learning, to genetic sequencing, to building modern web applications and so much more.</p>

          <h2>Where and When</h2>
          <p>You can find us Thurdsay nights at 7pm in West G 104! Stop by, hang out, and learn something mindblowing.</p>

          <h2>Interested in giving a talk? Know someone who would be awesome?</h2>
          <p>We'd love to hear from you! We're always interested in working with great people and learning something new.
          contact us at <a href={"mailto://acm@ccs.neu.edu"}> acm@ccs.neu.edu</a>!</p>
        </div>
      </div>
    );
  }
}


