import React from 'react';
import MailChimp from 'react-mailchimp-form';
import '../style/EmailForm.scss';

export default class EmailForm extends React.Component {
  render() {
    return (
      <div className="email-form-wrapper">
        <h1>Never miss a talk, subscribe to our mailing list!</h1>
        <MailChimp
        action="https://neu.us7.list-manage.com/subscribe/post?u=c63e996ca44a0ec7d4dd8f944&amp;id=7e5f3fd9f8"
        fields={[
          {
            name: "FNAME",
            placeholder: "First Name",
            type: "text",
            required: true
          },
          {
            name: "LNAME",
            placeholder: "Last Name",
            type: "text",
            required: true
          },
          {
            name: "EMAIL",
            placeholder: "Email",
            type: "email",
            required: true
          }
        ]}
        className={"email-form"}
        />
      </div>
    );
  }
}



